import { ensureElement, createElement } from '../utils/utils';
import { Component } from "./base/Component";
export class Basket extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._list = ensureElement('.basket__list', this.container);
        this._total = this.container.querySelector('.basket__price');
        this._button = this.container.querySelector('.basket__button');
        if (this._button) {
            this._button.addEventListener('mouseup', () => {
                events.emit('order:open');
            });
        }
        ;
        this.items = [];
    }
    ;
    set items(items) {
        if (items.length) {
            this._list.replaceChildren(...items);
            this._button.disabled = false;
        }
        else {
            this._list.replaceChildren(createElement('p', {
                textContent: 'Сart is empty'
            }));
            this._button.disabled = true;
        }
        ;
    }
    ;
    set total(total) {
        this.setText(this._total, `${total.toString()} synapses`);
    }
    ;
}
;
