import { Api } from "./base/api";
export class LarekApi extends Api {
    constructor(cdn, baseUrl, options) {
        super(baseUrl, options);
        this.cdn = cdn;
    }
    ;
    getCardsList() {
        return this.get('/product').then((data) => data && data.items ? data.items.map((item) => (Object.assign(Object.assign({}, item), { image: this.cdn + item.image }))) : []);
    }
    orderProducts(order) {
        return this.post(`/order`, order).then((data) => data);
    }
    ;
}
;
