import { ensureElement } from "../utils/utils";
import { Form } from "./common/Form";
export const paymentMethod = {
    "card": "online",
    "cash": "cash",
};
export class OrdersDelivery extends Form {
    constructor(container, events, actions) {
        super(container, events);
        this._card = ensureElement('#card', this.container);
        this._cash = ensureElement('#cash', this.container);
        this._card.classList.add('button_alt-active');
        if (actions.onClick) {
            this._card.addEventListener('mouseup', actions.onClick);
            this._cash.addEventListener('mouseup', actions.onClick);
        }
        ;
    }
    ;
    set address(value) {
        this.container.elements.namedItem('address').value = value;
    }
    ;
    changeButtonsClasses() {
        this._card.classList.toggle('button_alt-active');
        this._cash.classList.toggle('button_alt-active');
    }
}
;
